<template>
  <div id="controls">
    <span v-if="session.sessionId">
      <font-awesome-icon icon="microphone" v-if="microphoneSetting === 'free' && listeningFrame"
      @click="stopListening(microphoneSetting)"
      />
      <font-awesome-icon icon="microphone-slash" v-if="microphoneSetting === 'free' && !listeningFrame"
      @click="startListening(microphoneSetting)"
      />
      <font-awesome-icon icon="keyboard" v-if="microphoneSetting === 'keyboard'" :style="keyboardIcon"/>
      
      <select id="microphone" v-model="microphoneSetting" @change="stopListening(microphoneSetting)">
        <option value="free"></option>
        <option value="keyboard">f2</option>
      </select>
    </span>

    <span v-if="session.sessionId">
      <button v-if="!timing && !session.isSpectator" @click="startTimer" class="timerButton"></button>
      <button v-if="timing && !session.isSpectator" @click="stopTimer" class="timerButton"></button>
      <span style="font-size: 20px;" @click="setTimer">
        <span> </span>
        <span :style="lessThanOneMinute">{{ formattedTime }}</span>
      </span>
    </span>

    <span
      class="nomlog-summary"
      v-show="session.voteHistory.length && session.sessionId"
      @click="toggleModal('voteHistory')"
      :title="
        `${session.voteHistory.length} recent ${
          session.voteHistory.length == 1 ? 'nomination' : 'nominations'
        }`
      "
    >
      <font-awesome-icon icon="book-dead" />
      {{ session.voteHistory.length }}
    </span>
    <span
      class="session"
      :class="{
        spectator: session.isSpectator,
        reconnecting: session.isReconnecting
      }"
      v-if="session.sessionId"
      @click="leaveSession"
      :title="
        `${session.playerCount} other players in this session${
          session.ping ? ' (' + session.ping + 'ms latency)' : ''
        }`
      "
    >
      <font-awesome-icon icon="broadcast-tower" />
      {{ session.playerCount }}
    </span>
    <div class="menu" :class="{ open: grimoire.isMenuOpen }">
      <font-awesome-icon icon="cog" @click="toggleMenu" />
      <ul>
        <li class="tabs" :class="tab">
          <font-awesome-icon icon="book-open" @click="tab = 'grimoire'" />
          <font-awesome-icon icon="broadcast-tower" @click="tab = 'session'" />
          <font-awesome-icon
            icon="users"
            v-if="!session.isSpectator"
            @click="tab = 'players'"
          />
          <font-awesome-icon icon="theater-masks" @click="tab = 'characters'" />
          <font-awesome-icon icon="question" @click="tab = 'help'" />
        </li>

        <template v-if="tab === 'grimoire'">
          <!-- Grimoire -->
          <li class="headline"></li>
          <li @click="toggleGrimoire" v-if="players.length">
            <template v-if="!grimoire.isPublic"></template>
            <template v-if="grimoire.isPublic"></template>
            <em>[G]</em>
          </li>
          <li @click="toggleNight" v-if="!session.isSpectator">
            <template v-if="!grimoire.isNight"></template>
            <template v-if="grimoire.isNight"></template>
            <em>[S]</em>
          </li>
          <li @click="toggleNightOrder" v-if="players.length">
            
            <em>
              <font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isNightOrder ? 'check-square' : 'square'
                ]"
              />
            </em>
          </li>
          <li v-if="players.length">
            
            <em>
              <font-awesome-icon
                @click="setZoom(grimoire.zoom - 1)"
                icon="search-minus"
              />
              {{ Math.round(100 + grimoire.zoom * 10) }}%
              <font-awesome-icon
                @click="setZoom(grimoire.zoom + 1)"
                icon="search-plus"
              />
            </em>
          </li>
          <li @click="setBackground">
            
            <em><font-awesome-icon icon="image"/></em>
          </li>
          <li @click="$emit('trigger', ['uploadProfileImage'])">
            
            <em><font-awesome-icon icon="user"/></em>
          </li>
          <li @click="changeName">
            
            <em><font-awesome-icon icon="user-edit"/></em>
          </li>
          <li v-if="!edition.isOfficial" @click="imageOptIn">
            <small></small>
            <em
              ><font-awesome-icon
                :icon="[
                  'fas',
                  grimoire.isImageOptIn ? 'check-square' : 'square'
                ]"
            /></em>
          </li>
          <li @click="toggleStatic">
            
            <em
              ><font-awesome-icon
                :icon="['fas', grimoire.isStatic ? 'check-square' : 'square']"
            /></em>
          </li>
          <li v-if="!session.isSpectator" @click="toggleShowVacant">
            
            <em
              ><font-awesome-icon
                :icon="['fas', grimoire.isShowVacant ? 'check-square' : 'square']"
            /></em>
          </li>
          <li @click="toggleMuted">
            
            <em
              ><font-awesome-icon
                :icon="['fas', grimoire.isMuted ? 'volume-mute' : 'volume-up']"
            /></em>
          </li>
        </template>

        <template v-if="tab === 'session'">
          <!-- Session -->
          <li class="headline" v-if="session.sessionId">
            {{ session.isSpectator ? "" : "" }}
          </li>
          <li class="headline" v-else>
            
          </li>
          <template v-if="!session.sessionId">
            <li @click="hostSession"><em>[H]</em></li>
            <li @click="joinSession"><em>[J]</em></li>
          </template>
          <template v-else>
            <li v-if="session.ping">
              {{ session.isSpectator ? "" : "" }}
              <em>{{ session.ping }}ms</em>
            </li>
            <li @click="copySessionUrl">
              
              <em><font-awesome-icon icon="copy"/></em>
            </li>
            <li v-if="!session.isSpectator" @click="distributeAsk">
              
              <em><font-awesome-icon icon="theater-masks"/></em>
            </li>
            <li v-if="!session.isSpectator" @click="distributeBluffsAsk">
              
              <em><font-awesome-icon icon="demon"/></em>
            </li>
            <li v-if="!session.isSpectator" @click="distributeGrimoireAsk">
              
              <em><font-awesome-icon icon="demon"/></em>
            </li>
            <li
              v-if="session.voteHistory.length || !session.isSpectator"
              @click="toggleModal('voteHistory')"
            >
              <em>[V]</em>
            </li>
            <li @click="leaveSession">
              <span v-if="session.isSpectator"></span>
              <span v-if="!session.isSpectator"></span>
              <em>{{ session.sessionId }}</em>
            </li>
          </template>
        </template>

        <template v-if="tab === 'players' && !session.isSpectator">
          <!-- Users -->
          <li class="headline"></li>
          <li @click="addPlayer" v-if="players.length < 20"><!--<em>[A]</em>--></li>
          <li @click="randomizeSeatings" v-if="players.length > 2">
            
            <em><font-awesome-icon icon="dice"/></em>
          </li>
          <li @click="clearPlayers" v-if="players.length">
            
            <em><font-awesome-icon icon="trash-alt"/></em>
          </li>
        </template>

        <template v-if="tab === 'characters'">
          <!-- Characters -->
          <li class="headline"></li>
          <li v-if="!session.isSpectator" @click="toggleModal('edition')">
            
            <em>[E]</em>
          </li>
          <li
            @click="toggleModal('roles')"
            v-if="!session.isSpectator && players.length > 4"
          >
            
            <em>[C]</em>
          </li>
          <li v-if="!session.isSpectator" @click="toggleModal('fabled')">
            
            <em>[F]</em>
          </li>
          <li v-if="!session.isSpectator" @click="customiseBootlegger">
            <small>
              
            </small>
            <em>[B]</em>
          </li>
          <li @click="clearRoles" v-if="players.length">
            
            <em><font-awesome-icon icon="trash-alt"/></em>
          </li>
        </template>

        <template v-if="tab === 'help'">
          <!-- Help -->
          <li class="headline"></li>
          <li @click="toggleModal('reference')">
            
            <em>[R]</em>
          </li>
          <li @click="toggleModal('nightOrder')">
            
            <em>[N]</em>
          </li>
          <li @click="toggleModal('gameState')">
            JSON
            <em><font-awesome-icon icon="file-code"/></em>
          </li>
          <!-- <li>
            <a href="https://discord.gg/Gd7ybwWbFk" target="_blank">
              Join Discord
            </a>
            <em>
              <a href="https://discord.gg/Gd7ybwWbFk" target="_blank">
                <font-awesome-icon :icon="['fab', 'discord']" />
              </a>
            </em>
          </li> -->
          <li>
            <a href="https://github.com/bra1n/townsquare" target="_blank">
              
            </a>
            <em>
              <a href="https://github.com/bra1n/townsquare" target="_blank">
                <font-awesome-icon :icon="['fab', 'github']" />
              </a>
            </em>
          </li>
        </template>
      </ul>
    </div>

    <div v-if="distributing" class="dialog">
      <span>
        <label></label>
        <input type="checkbox" v-model="isSendingBluff" class="bluffCheckbox"/>
      </span>
      <div>
        <button @click="distributeRoles(true)"></button>
        <button @click="distributeRoles(false)"></button>
      </div>
    </div>
    <div v-if="distributingBluffs" class="dialog">
      <span>
        <label></label>
      </span>
      <div>
        <button @click="distributeBluffs('demon')"></button>
        <button @click="distributeBluffs('lunatic')"></button>
        <button @click="distributeBluffs('snitch')"></button>
        <button @click="distributeBluffs('')"></button>
      </div>
    </div>
    <div v-if="distributingGrimoire" class="dialog">
      <span>
        <label></label>
      </span>
      <div>
        <button @click="distributeGrimoire('widow')"></button>
        <button @click="distributeGrimoire('spy')"></button>
        <button @click="distributeGrimoire('')"></button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  computed: {
    ...mapState(["grimoire", "session", "edition"]),
    ...mapState("players", ["players"]),
    formattedTime() {
      const minutes = Math.floor(this.session.timer / 60);
      const seconds = this.session.timer % 60;
      return `${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
    },
    lessThanOneMinute() {
      return {
        color: this.session.timer < 60 ? 'red' : 'white'
      }
    },
    keyboardIcon() {
      return {
        color: this.listeningFrame ? 'red' : 'white'
      }
    }
  },
  data() {
    return {
      tab: "grimoire",
      timing: false,
      distributing: false,
      distributingBluffs: false,
      distributingGrimoire: false,
      isSendingBluff: true,
      recognition: null,
      microphoneSetting: "free",
      // 
      audioContext: null,
      audioStream: null,
      analyser: null,
      source: null,
      listeningFrame: null // also in session.js for global reference
    };
  },
  methods: {
    setBackground() {
      const background = prompt("URL");
      if (background || background === "") {
        this.$store.commit("setBackground", background);
      }
    },
    changeName() {
      var name = prompt("").trim();
      if (!name) return; // will not execute because .trim() incurs error first
      while (name === "" || name === ""){
        alert("");
        name = prompt("").trim();
        if (!name) return; // will not execute because .trim() incurs error first
      }
      this.$store.commit("session/setPlayerName", name);
    },
    hostSession() {
      if (this.session.sessionId) return;
      // const sessionId = prompt("").toString();
      const sessionId = Math.round(Math.random() * 10000).toString();
      var numPlayers = prompt(
        ("" + sessionId + ""), 12
      );
      if (!Number(numPlayers)) return;
      if (numPlayers < 0) return;
      if (sessionId) {
        this.$store.commit("session/clearVoteHistory", []);
        this.$store.commit("session/setSpectator", false);
        this.$store.commit("session/setSessionId", sessionId);
        numPlayers = Math.min(numPlayers, 20);
        this.$store.commit("players/clear");
        for(let i=0; i < numPlayers; i++){
          this.addPlayer();
        }
        this.copySessionUrl();
      };
    },
    copySessionUrl() {
      const url = window.location.href.split("#")[0];
      const link = url + "#" + this.session.sessionId;
      navigator.clipboard.writeText(link);
    },
    distributeAsk() {
      this.distributingBluffs = false;
      this.distributingGrimoire = false;
      if (this.distributing) this.distributing = false;
      else this.distributing = true;
    },
    distributeRoles(confirm) {
      this.distributing = false;
      if (!confirm) return;
      if (this.session.isSpectator) return;
      this.$store.commit("session/distributeRoles", true);
      setTimeout(
        (() => {
          this.$store.commit("session/distributeRoles", false);
        }).bind(this),
        2000
      );
      if (!this.isSendingBluff) return;
      this.$store.commit("session/distributeBluffs", {val: true, param: "demonAll"});
      setTimeout(
        (() => {
          this.$store.commit("session/distributeBluffs", {val:false});
        }).bind(this),
        2000
      );
    },
    distributeBluffsAsk() {
      this.distributing = false;
      this.distributingGrimoire = false;
      if (this.distributingBluffs) this.distributingBluffs = false;
      else this.distributingBluffs = true;
    },
    distributeBluffs(role = "") {
      if (!role) {
        this.distributingBluffs = false;
        return;
      }
      
      var roleText = "";
      switch (role) {
        case "demon":
          roleText = "";
          break;
        case "lunatic":
          roleText = "";
          break;
        case "snitch":
          roleText = "";
          break;
      }

      if (confirm("" + roleText + "")) {
        if (this.session.isSpectator) return;
        this.$store.commit("session/distributeBluffs", {val: true, param: role});
        setTimeout(
          (() => {
            this.$store.commit("session/distributeBluffs", {val:false});
          }).bind(this),
          2000
        );
        this.distributingBluffs = false;
      }
    },
    distributeGrimoireAsk(){
      this.distributing = false;
      this.distributingBluffs = false;
      if (this.distributingGrimoire) this.distributingGrimoire = false;
      else this.distributingGrimoire = true;
    },
    distributeGrimoire(role = ""){

      if (!role) {
        this.distributingGrimoire = false;
        return;
      }
      
      var roleText = "";
      switch (role) {
        case "widow":
          roleText = "";
          break;
        case "spy":
          roleText = "";
          break;
      }

      if (confirm("" + roleText + "")) {
        if (this.session.isSpectator) return;
        this.$store.commit("session/distributeGrimoire", {val: true, param: role});
        setTimeout(
          (() => {
            this.$store.commit("session/distributeGrimoire", {val:false});
          }).bind(this),
          2000
        );
        this.distributingGrimoire = false;
      }
    },
    imageOptIn() {
      const popup =
        "IP";
      if (this.grimoire.isImageOptIn || confirm(popup)) {
        this.toggleImageOptIn();
      }
    },
    joinSession() {
      if (this.session.sessionId) return this.leaveSession();
      let sessionId = prompt(
        "/"
      );
      if (!sessionId) return;
      if (!this.session.playerName) this.changeName();
      if (!this.session.playerName) return;
      if (sessionId.match(/^https?:\/\//i)) {
        sessionId = sessionId.split("#").pop();
      }
      if (sessionId) {
        this.$store.commit("session/clearVoteHistory");
        this.$store.commit("session/setSpectator", true);
        this.$store.commit("toggleGrimoire", false);
        this.$store.commit("session/setSessionId", sessionId);
      }
    },
    leaveSession() {
      if (confirm("/")) {
        // vacate seat upon leaving the room
        const playerIndex = this.session.claimedSeat;
        if (playerIndex >= 0){
          if (this.session.playerId === this.players[playerIndex].id) {
            this.$store.commit("session/claimSeat", -1);
          } else {
            this.$store.commit("session/claimSeat", playerIndex);
          }
        }

        this.$store.commit("session/setSpectator", false);
        this.$store.commit("session/setSessionId", "");
        
        // clear seats and return to intro
        if (this.session.nomination) {
          this.$store.commit("session/nomination");
        }
        this.$store.commit("players/clear");

        // clear customBootlegger
        if (this.session.bootlegger) {
          this.$store.commit("session/setBootlegger", "");
        }

        // reset secret vote
        if (this.session.isSecretVote) {
          this.$store.commit("session/setSecretVote", false)
        }
      }
    },
    addPlayer() {
      if (this.session.isSpectator) return;
      if (this.players.length >= 20) return;
      
      // setting name to a default value, combining with the seat number
      this.$store.commit("players/add", "");
    },
    randomizeSeatings() {
      if (this.session.isSpectator) return;
      if (confirm("")) {
        this.$store.dispatch("players/randomize");
      }
    },
    clearPlayers() {
      if (this.session.isSpectator) return;
      if (confirm("")) {
        // abort vote if in progress
        if (this.session.nomination) {
          this.$store.commit("session/nomination");
        }
        this.$store.commit("players/clear");
      }
    },
    clearRoles() {
      if (confirm("")) {
        this.$store.dispatch("players/clearRoles");
      }
    },
    customiseBootlegger() {
      if (this.session.isSpectator) return;

      let content = prompt("", this.session.bootlegger);
      if (content == null) return;

      const trimmedContent = content.trim();
      this.$store.commit("session/setBootlegger", trimmedContent);
    },
    toggleNight() {
      this.$store.commit("toggleNight");
      if (this.grimoire.isNight) {
        this.$store.commit("session/setMarkedPlayer", -1);
      }
    },
    setTimer() {
      if (this.session.isSpectator || !this.session.sessionId) return;
      const time = prompt("");
      const timeNum = Number(time);
      if (!timeNum) return;
      if (timeNum <= 0) return;
      this.timing = true;
      this.stopTimer();
      this.startTimer(timeNum * 60);
    },
    startTimer(time = null) {
      if (this.session.isSpectator) return;
      if (typeof time != 'number') time = this.session.timer;
      this.$store.commit("session/startTimer", time);
      this.timing = true;
    },
    stopTimer() {
      if (this.session.isSpectator) return;
      this.$store.commit("session/stopTimer");
      this.timing = false;
    },
    async initAudio() {
      // Initialize audioContext and audioStream if not already done
      if (!this.audioContext) {
        this.audioContext = new AudioContext();
        const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
        this.audioStream = stream;

        this.source = this.audioContext.createMediaStreamSource(stream);
        this.analyser = this.audioContext.createAnalyser();
        this.analyser.fftSize = 256;
        this.source.connect(this.analyser);
      }
    },
    runAudioDetection() {
      // Web Audio API 
      const dataArray = new Uint8Array(this.analyser.frequencyBinCount);
      const SILENCE_THRESHOLD = 120; // Adjust as needed
      const HUMAN_VOICE_RANGE = { min: 250, max: 400 }; // Adjust based on actual FFT resolution

      // Check for microphone activity
      const detectSpeechActivity = () => {
        if (!this.analyser) return;
        this.analyser.getByteFrequencyData(dataArray);

        // Analyze frequency bins corresponding to the human voice range
        const sampleRate = this.audioContext.sampleRate;
        const binCount = this.analyser.frequencyBinCount;
        const binSize = sampleRate / (2 * binCount);
        let totalVolume = 0;
        for (let i = 0; i < binCount; i++) {
          const frequency = i * binSize;
          if (frequency >= HUMAN_VOICE_RANGE.min && frequency <= HUMAN_VOICE_RANGE.max) {
            totalVolume += dataArray[i];
          }
        }

        if (totalVolume > SILENCE_THRESHOLD && !this.session.isTalking) {
          if (!this.session.isTalking) {
            this.$store.commit("session/startTalking", this.session.claimedSeat);
          }
        } else if (totalVolume <= SILENCE_THRESHOLD && this.session.isTalking) {
          if (this.session.isTalking) {
            this.$store.commit("session/stopTalking", this.session.claimedSeat);
          }
        }

        this.listeningFrame = requestAnimationFrame(detectSpeechActivity);
        this.$store.commit("session/setListeningFrame", this.listeningFrame);
      }

      detectSpeechActivity();
    },
    startListening(mode) {
      if (this.listeningFrame) return;
      if (mode != this.microphoneSetting) return;
      
      this.initAudio().then(() => {
        this.runAudioDetection();
      })
    },
    stopListening(mode) {
      if (!this.listeningFrame) return;
      if (mode != this.microphoneSetting) return;

      if (this.listeningFrame) {
        cancelAnimationFrame(this.listeningFrame);
        this.listeningFrame = null;
        this.$store.commit("session/setListeningFrame", null);
      }
      this.$store.commit("session/stopTalking", this.session.claimedSeat);
    },
    ...mapMutations([
      "toggleGrimoire",
      "toggleMenu",
      "toggleImageOptIn",
      "toggleMuted",
      "toggleShowVacant",
      "toggleNightOrder",
      "toggleStatic",
      "setZoom",
      "toggleModal"
    ])
  }
};
</script>

<style scoped lang="scss">
@import "../vars.scss";

// success animation
@keyframes greenToWhite {
  from {
    color: green;
  }
  to {
    color: white;
  }
}

// Controls
#controls {
  position: absolute;
  right: 3px;
  top: 3px;
  text-align: right;
  padding-right: 50px;
  z-index: 75;

  svg {
    filter: drop-shadow(0 0 5px rgba(0, 0, 0, 1));
    &.success {
      animation: greenToWhite 1s normal forwards;
      animation-iteration-count: 1;
    }
  }

  > span {
    display: inline-block;
    cursor: pointer;
    z-index: 5;
    margin-top: 7px;
    margin-left: 10px;
  }

  span.nomlog-summary {
    color: $townsfolk;
  }

  span.session {
    color: $demon;
    &.spectator {
      color: $townsfolk;
    }
    &.reconnecting {
      animation: blink 1s infinite;
    }
  }
}

@keyframes blink {
  50% {
    opacity: 0.5;
    color: gray;
  }
}

.menu {
  width: 220px;
  transform-origin: 200px 22px;
  transition: transform 500ms cubic-bezier(0.68, -0.55, 0.27, 1.55);
  transform: rotate(-90deg);
  position: absolute;
  right: 0;
  top: 0;

  &.open {
    transform: rotate(0deg);
  }

  > svg {
    cursor: pointer;
    background: rgba(0, 0, 0, 0.5);
    border: 3px solid black;
    width: 40px;
    height: 50px;
    margin-bottom: -8px;
    border-bottom: 0;
    border-radius: 10px 10px 0 0;
    padding: 5px 5px 15px;
  }

  a {
    color: white;
    text-decoration: none;
    &:hover {
      color: red;
    }
  }

  ul {
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    overflow: hidden;
    box-shadow: 0 0 10px black;
    border: 3px solid black;
    border-radius: 10px 0 10px 10px;

